<template>
  <div class="lk">
    <div class="lk-tasks">
      <h2 class="lk-tasks__h">Биржа заданий</h2>
      <div class="lk-tasks__cards">
        <b-card v-for="task in tasks"
          :key="task.id"
          :title="task.name"
          img-src="https://picsum.photos/600/300/?image=17"
          img-alt="задание"
          img-top
          tag="article"
          style="max-width: 20rem;"
          class="mb-2 lk-tasks__card"
        >
          <b-card-text>
            <p v-html="task.desc"></p>
            <p>Необходимо людей: {{task.people}}</p>
            <p>Кварки за выполнение: {{task.points}}</p>
          </b-card-text>

          <b-button href="#" variant="primary">Приступить</b-button>
        </b-card>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Dashboard",
  data() {
    return {
      tasks: [
        {
          id: 0,
          coords: [ 54.817533, 58.448946 ],
          name: 'Сдача макулатуры',
          desc: 'Приглашаем всех желающих сдать макулатуру',
          people: 'неограничено',
          points: 20
        },
        {
          id: 1,
          coords: [54.811554, 58.450909],
          name: 'Игрушки в детский дом',
          desc: 'Пожертвовать новыми игрушками в детским дом',
          people: 5,
          points: 50
        },
        {
          id: 2,
          coords: [54.817425, 58.452623],
          name: 'Весенний субботник',
          desc: 'Приглашаем жителей принять участие в субботнике',
          people: 10,
          points: 50
        }
      ]
    }
  }
}
</script>

<style scoped>

.lk {
  max-width: 980px;
  margin: 0 auto;
}

.lk-tasks__h {
  margin-top: 30px;
  margin-bottom: 30px;
  text-align: center;
}

.lk-tasks__cards {
  display: flex;
  justify-content: space-between;
}

.lk-tasks__card {
}

</style>


